ul,
ol {
    list-style: initial;
    list-style-type: "" !important; /*https://www.matuzo.at/blog/2023/removing-list-styles-without-affecting-semantics*/
}
.text-base {
    // font-weight: 300;
    // p {
    //     font-size: var(--step--1);
    //     line-height: 1.6;
    // }
    ul,
    ol {
        font-size: var(--step--1);
        font-weight: 500;
        > li {
            position: relative;
        }
        ul,
        ol {
            font-weight: 300;
            margin-top: var(--space-s);
            // margin-left: var(--space-s);
        }
        ul {
            > li {
                padding-left: 0.5rem;
                &::before {
                    content: "";
                    width: 3px;
                    height: 3px;
                    border-radius: $radius-rounded;
                    background-color: $primary;
                    display: block;
                    margin-right: var(--space-2xs);
                    position: absolute;
                    left: 0;
                    top: 0.6em;
                }
            }
        }
        ol {
            > li {
                padding-left: 2.25rem;
                &::before {
                    color: $grey-darker;
                    font-weight: 600;
                    border-right-width: 1px;
                    border-right-color: $grey;
                }
            }
        }
    }
    ul {
        > li {
            padding-left: 2.5rem;
            &::before {
                content: "";
                width: 30px;
                height: 30px;
                display: block;
                margin-right: var(--space-2xs);
                position: absolute;
                background-image: url("data:image/svg+xml,%3Csvg width='30' height='30' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cpath d='M0 0v30h30V0z'/%3E%3Cpath stroke='%23a50e00' stroke-width='2' stroke-linecap='round' stroke-linejoin='round' d='M6.25 15h17.5M18.75 20l5-5M18.75 10l5 5'/%3E%3C/g%3E%3C/svg%3E");
                left: 0;
                top: -0.25em;
                .dialog-content & {
                    filter: grayscale(1) brightness(100);
                }
            }
            + li {
                margin-top: var(--space-xs);
            }
        }
    }
    ol {
        > li {
            padding-left: 1.75rem;
            &::before {
                content: counters(list-item, ".");
                display: block;
                color: $primary;
                font-size: 0.75rem;
                margin-right: var(--space-2xs);
                padding-right: var(--space-2xs);
                border-right: 2px solid $primary;
                font-weight: 600;
                position: absolute;
                left: 0;
                top: 0.25em;
            }
            + li {
                margin-top: var(--space-xs);
            }
        }
    }
}

